import React from "react";

import "../message-item/MessageItem.style.scss";

const MessageItem = ({
  name,
  email,
  date,
  messageSent,
  messageID,
  deleteMessage,
}) => {
  return (
    <div className="message-box">
      <div className="message-name-email-container">
        <span className="message-name">{name}</span>
        <span className="message-email">{email}</span>
      </div>
      <div className="message-text-container">
        <p>{messageSent}</p>
      </div>
      <div className="message-remove-date-container">
        <button
          className="message-remove-button"
          onClick={() => deleteMessage(messageID)}
        >
          Delete
        </button>
        <span className="message-date">{date}</span>
      </div>
    </div>
  );
};

export default MessageItem;
