import { Component } from "react";
import swal from "sweetalert";
import { deleteMessage, firestore } from "../../firebase/firebase-utils";

import MessageItem from "../messages-page/message-item/MessageItem";
import "../messages-page/Messages.style.scss";

class Messages extends Component {
  constructor(props) {
    super(props);
    this.state = {
      messageList: [],
    };
  }
  handleGetMessages() {
    this.messageUnSubscribe = firestore
      .collection(process.env.REACT_APP_PORTFOLIO_MESSAGE_COLLECTION)
      .orderBy("sentAt", "desc")
      .onSnapshot((messageSnapshot) => {
        this.setState({
          messageList: [
            ...messageSnapshot.docs.map((messageDoc) => {
              return {
                ...messageDoc.data(),
                sentAt: messageDoc.data().sentAt.toDate().toString(),
                messageID: messageDoc.id,
              };
            }),
          ],
        });
      });
  }

  handleDeleteMessage = (messageToDelete) => {
    swal({
      title: "Delete Message",
      text: "Are you sure you want to delete this message?",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    }).then((willDelete) => {
      if (willDelete) {
        deleteMessage(messageToDelete);
      } else {
        swal({
          title: "Cancel",
          text: "Canceled deleting message.",
          icon: "warning",
        });
      }
    });
  };

  componentDidMount() {
    this.handleGetMessages();
  }
  componentWillUnmount() {
    this.messageUnSubscribe();
  }

  render() {
    return (
      <section id="messages" className="messages-page-container">
        <div className="title-container">
          <h1>
            Messages<span>Some hidden messages section.</span>
          </h1>
        </div>
        <div className="messages-content">
          {this.state.messageList.length ? (
            <p> {this.state.messageList.length} Message(s) found</p>
          ) : null}
          <div className="messages-container">
            {this.state.messageList.length ? (
              this.state.messageList.map(
                ({ name, email, messageSent, sentAt, messageID }, index) => (
                  <MessageItem
                    key={index}
                    name={name}
                    email={email}
                    date={sentAt}
                    messageSent={messageSent}
                    messageID={messageID}
                    deleteMessage={this.handleDeleteMessage}
                  />
                )
              )
            ) : (
              <p
                className="section-h2-subtitle"
                style={{ color: "red", textAlign: "center" }}
              >
                No Message Found
              </p>
            )}
          </div>
        </div>
      </section>
    );
  }
}
export default Messages;
